.user-year-in-books-page-main-content {
	width: 50%;
	margin-right: auto;
	margin-left: auto;
}

.user-year-in-books-overall-stats-top {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.user-year-in-books-overall-stats-top a {
	display: inline;
	color: var(--goodreads-teal);
	background-color: transparent;
	font-family: var(--lato-font);
	font-size: 14px;
	padding: 0;
}

.user-year-in-books-overall-stats-top a:hover {
	text-decoration: underline;
}

.user-year-in-books-image-and-title {
	display: flex;
	flex-direction: column;
	align-items: center;
	margin-top: 25px;
}

.user-year-in-books-image-and-title span {
	font-family: var(--merriweather-font);
	font-size: 28px;
}

.user-year-in-books-image-and-title img {
	width: 100%;
}

.user-year-in-books-page-main-content .bottom-section .top-half {
	margin-top: 15px;
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-bottom: 30px;
}

.user-year-in-books-page-main-content .bottom-section .top-half .stat {
	display: flex;
	flex-direction: column;
	align-items: center;
	font-size: 14px;
	font-family: var(--merriweather-font);
}

.user-year-in-books-page-main-content .bottom-section .top-half .number {
	font-size: 32px;
	line-height: 40px;
	font-family: var(--merriweather-font);
}

.user-year-in-books-page-main-content .profile-info {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.user-year-in-books-page-main-content .profile-a {
	padding: 0;
	margin-bottom: 15px;
}

.user-year-in-books-page-main-content .profile-name-a {
	padding: 0;
	display: inline;
	background-color: transparent;
	color: var(--goodreads-black);
	font-family: var(--merriweather-font);
	font-size: 18px;
	line-height: 23px;
}

.user-year-in-books-page-main-content .profile-name-a:hover {
	text-decoration: underline;
}

.user-year-in-books-page-main-content .profile-a img {
	border-radius: 100%;
	width: 70px;
}

.user-year-in-books-page-main-content .book-and-info-wrapper {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 80px 0;
	border-top: 0.5px solid rgb(216, 216, 216);
}

.user-year-in-books-page-main-content .book-and-info {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	margin-right: 50px;
}

.user-year-in-books-page-main-content .book-and-info span {
	display: block;
}

.user-year-in-books-page-main-content .book-and-info .small-text {
	color: rgb(110, 110, 110);
	font-size: 16px;
	line-height: 19px;
	font-family: var(--lato-font);
	margin-top: 50%;
}

.user-year-in-books-page-main-content .book-and-info .big-text {
	font-family: var(--merriweather-font);
	font-size: 40px;
	line-height: 51px;
	margin-top: 8px;
}

.user-year-in-books-page-main-content .book-and-info .medium-text {
	font-family: var(--merriweather-font);
	font-size: 20px;
	line-height: 25px;
	margin-top: 8px;
}

.user-year-in-books-page-main-content .book-and-info-wrapper a.book-and-info-a {
	padding: 0;
	width: fit-content;
	height: fit-content;
	margin-right: 15px;
}

.user-year-in-books-page-main-content .book-and-info-wrapper img {
	width: 140px;
}

.user-year-in-books-page-main-content .book-length-section,
.user-year-in-books-page-main-content .average-rating-section {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	padding: 80px 15%;
	padding-left: 10%;
	border-top: 0.5px solid rgb(216, 216, 216);
}

.user-year-in-books-page-main-content .book-length-section .average-length-span,
.user-year-in-books-page-main-content
	.average-rating-section
	.average-rating-span {
	font-family: var(--merriweather-font);
	font-size: 40px;
	line-height: 51px;
}

.user-year-in-books-page-main-content .book-length-section .number-pages,
.user-year-in-books-page-main-content .average-rating-section .stars-rating {
	display: flex;
	flex-direction: column;
	align-items: center;
}

.user-year-in-books-page-main-content
	.book-length-section
	.number-pages
	.number,
.user-year-in-books-page-main-content .average-rating-section .rating {
	font-family: var(--merriweather-font);
	font-size: 56px;
	line-height: 71px;
}

.user-year-in-books-page-main-content
	.book-length-section
	.number-pages
	.pages {
	font-family: var(--lato-font);
	font-size: 24px;
	line-height: 31px;
}

.user-year-in-books-page-main-content .average-rating-section .rating-stars,
.user-year-in-books-page-main-content .highest-rated-section .rating-stars {
	display: flex;
	flex-direction: row;
}

.user-year-in-books-page-main-content
	.average-rating-section
	.rating-stars
	.static-star.large,
.user-year-in-books-page-main-content
	.highest-rated-section
	.rating-stars
	.static-star.large {
	width: 20px;
	height: 20px;
	background-repeat: no-repeat;
	background-size: 20px 20px;
}

.user-year-in-books-page-main-content .highest-rated-section > a {
	padding: 0;
	width: fit-content;
	height: fit-content;
}

.user-year-in-books-page-main-content .highest-rated-section {
	display: flex;
	flex-direction: row;
	align-items: center;
	border-top: 0.5px solid rgb(216, 216, 216);
	padding: 80px 5%;
}

.user-year-in-books-page-main-content .highest-rated-section img {
	width: 200px;
}

.user-year-in-books-page-main-content
	.highest-rated-section
	.highest-rated-span {
	font-family: var(--merriweather-font);
	font-size: 40px;
	line-height: 51px;
}

.user-year-in-books-page-main-content .highest-rated-section .rating-average {
	display: block;
	color: rgb(110, 110, 110);
	font-size: 16px;
	line-height: 19px;
	font-family: var(--lato-font);
	margin-top: 15px;
}

.user-year-in-books-page-main-content
	.highest-rated-section
	.highest-rated-info {
	margin-left: 30px;
}

.user-year-in-books-page-main-content .all-books-section {
	border-top: 0.5px solid rgb(216, 216, 216);
	padding: 20px 2%;
}

.user-year-in-books-page-main-content .all-books-section .book-and-info-a {
	padding: 0;
	width: fit-content;
	height: fit-content;
}

.user-year-in-books-page-main-content .all-books-section .top-section {
	display: flex;
	flex-direction: row;
}

.user-year-in-books-page-main-content .all-books-section .top-section > a img {
	width: 260px;
}

.user-year-in-books-page-main-content
	.all-books-section
	.top-section
	.other-top-books {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-between;
	margin-left: 60px;
}

.user-year-in-books-page-main-content
	.all-books-section
	.top-section
	.other-top-books
	a {
	margin-bottom: 15px;
}

.user-year-in-books-page-main-content
	.all-books-section
	.top-section
	.other-top-books
	img,
.user-year-in-books-page-main-content .all-books-section .bottom-section img {
	width: 143px;
}

.user-year-in-books-page-main-content .all-books-section .bottom-section {
	display: flex;
	flex-direction: row;
	flex-flow: row wrap;
	justify-content: space-between;
	margin-top: 30px;
}

.user-year-in-books-page-main-content .all-books-section .bottom-section a {
	margin-bottom: 15px;
}

.user-year-in-books-page-main-content
	.all-books-section
	.bottom-section::after {
	content: '';
	flex: 0 0 48%;
}

.user-year-in-books-page-main-content .all-books-section .my-books-span {
	display: block;
	color: var(--goodreads-black);
	font-family: var(--lato-font);
	text-transform: uppercase;
	font-size: 15px;
	font-weight: bold;
	margin-bottom: 10px;
}

.user-year-in-books-page-main-content .missing-books-section {
	text-align: center;
	border: 1px solid rgb(151, 151, 151);
	border-radius: 5px;
	padding: 60px;
	margin-bottom: 60px;
}

.user-year-in-books-page-main-content .missing-books-section span {
	display: block;
}

.user-year-in-books-page-main-content
	.missing-books-section
	.missing-books-span {
	font-size: 40px;
	line-height: 50px;
	font-family: var(--merriweather-font);
	color: var(--goodreads-black);
}

.user-year-in-books-page-main-content
	.missing-books-section
	.missing-books-message {
	font-size: 16px;
	font-family: var(--lato-font);
	color: var(--goodreads-black);
	margin-top: 15px;
}

.user-year-in-books-page-main-content
	.missing-books-section
	.missing-books-message
	strong {
	font-weight: bold;
}

.user-year-in-books-page-main-content .missing-books-section .missing-books-a {
	display: inline;
	color: var(--goodreads-teal);
	font-family: var(--lato-font);
	font-size: 16px;
	background-color: transparent;
}

.user-year-in-books-page-main-content
	.missing-books-section
	.missing-books-a:hover {
	text-decoration: underline;
}
