@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');

.editable-bookshelf-date-field {
	position: relative;
}

.editable-bookshelf-date-field .edit-button {
	color: var(--goodreads-teal);
	font-family: var(--lato-font);
	font-size: 11px;
	line-height: 14px;
	height: 14px;
	padding: 0;
	background-color: transparent;
	margin-top: 3px;
}

.editable-bookshelf-date-field .edit-button:hover {
	text-decoration: underline;
}

.editable-bookshelf-date-field .set-date-popup {
	position: absolute;
	padding: 5px;
	border: 2px solid rgb(204, 204, 204);
	background-color: rgb(255, 255, 255);
}

.editable-bookshelf-date-field .set-date-popup .popup-top-section,
.editable-bookshelf-date-field .set-date-popup .popup-bottom-section {
	display: flex;
	flex-direction: row;
}

.editable-bookshelf-date-field .set-date-popup .popup-bottom-section {
	justify-content: space-between;
	margin-top: 5px;
}

.editable-bookshelf-date-field .set-date-popup .popup-top-section select {
	font-size: 11px;
	margin-right: 3px;
}

.editable-bookshelf-date-field
	.set-date-popup
	.popup-top-section
	.set-to-today-button {
	font-family: var(--lato-font);
	color: var(--goodreads-teal);
	font-size: 10px;
	line-height: 18px;
	padding: 0;
	background-color: transparent;
	margin-left: 5px;
	width: 60px;
}

.editable-bookshelf-date-field
	.set-date-popup
	.popup-top-section
	.set-to-today-button:hover {
	text-decoration: underline;
}

.editable-bookshelf-date-field
	.set-date-popup
	.popup-bottom-section
	.save-button {
	background-color: rgb(244, 241, 234);
	border: 1px solid rgb(214, 208, 196);
	border-radius: 3px;
	color: rgb(51, 51, 51);
	font-family: var(--lato-font);
	font-size: 11px;
	line-height: 11px;
	padding: 4px 12px;
	height: 21px;
}

.editable-bookshelf-date-field
	.set-date-popup
	.popup-bottom-section
	.save-button:hover {
	background-color: rgb(237, 230, 214);
}

.editable-bookshelf-date-field .set-date-popup.hidden {
	display: none;
}
