.recommendation-page {
	height: 80%;
}

.recommendation-page .recommendations-main-content {
	padding: 1.5% 10%;
	min-height: 90%;
}

.recommendation-page .recommendations-page-indicator {
	display: block;
	color: var(--goodreads-brown);
	font-family: var(--merriweather-font);
	font-weight: bold;
	font-size: 20px;
	line-height: 24px;
	margin-bottom: 20px;
}

.recommendation-page .recommendations-total-span {
	margin-left: 10px;
	font-family: var(--lato-font);
	font-size: 11px;
	line-height: 14px;
}

.recommendation-page .main-content {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	width: 95%;
	margin-left: 15px;
	min-height: 90%;
}

.recommendation-page .main-content .recommendation-list {
	width: 100%;
	margin-right: 30px;
}

.recommendation-page .main-content .recommendation-card {
	width: 100%;
	padding-bottom: 15px;
	border-bottom: 1px solid rgb(215, 215, 215);
}

.recommendation-page .main-content .recommendation-card .top-section {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
}

.recommendation-page .main-content .recommendation-card .top-section span {
	font-family: var(--lato-font);
	font-size: 12px;
}

.recommendation-page .main-content .recommendation-card .top-section a {
	display: inline;
	font-family: var(--lato-font);
	font-size: 12px;
	font-weight: bold;
	color: var(--goodreads-teal);
	padding: 0;
	background-color: transparent;
}

.recommendation-page .main-content .recommendation-card .top-section a:hover,
.recommendation-page
	.main-content
	.recommendation-card
	.top-section
	button:hover {
	text-decoration: underline;
}

.recommendation-page .main-content .recommendation-card .top-section button {
	font-family: var(--lato-font);
	font-size: 12px;
	color: rgb(153, 153, 153);
	display: inline;
	background-color: transparent;
	padding: 0;
}

.recommendation-page .main-content .recommendation-card .bottom-section {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: flex-start;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.want-to-read-button-and-options {
	margin-top: 0;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section {
	display: flex;
	flex-direction: row;
}

.recommendation-page .main-content .recommendation-card .bottom-section a {
	padding: 0;
	background-color: transparent;
	width: fit-content;
}

.recommendation-page .main-content .recommendation-card .bottom-section img {
	width: 50px;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.book-info {
	margin-left: 7px;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.book-info
	a {
	line-height: auto;
	height: auto;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.book-title-a {
	color: var(--goodreads-black);
	font-family: var(--merriweather-font);
	font-size: 14px;
	line-height: 15px;
	height: auto;
	font-weight: bold;
	margin-bottom: 3px;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.book-title-a:hover {
	text-decoration: underline;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.authorship-span {
	display: block;
	font-size: 13px;
	color: var(--goodreads-black);
	font-family: var(--lato-font);
	margin-bottom: 3px;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.authorship-span
	a {
	display: inline;
	font-size: 12px;
	color: var(--goodreads-black);
	font-family: var(--merriweather-font);
	line-height: 13px;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.authorship-span
	a:hover {
	text-decoration: underline;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.rec-message-span {
	font-family: var(--lato-font);
	color: var(--goodreads-black);
	font-size: 12px;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.rec-message-span
	a {
	display: inline;
	color: var(--goodreads-teal);
	line-height: 18px;
}

.recommendation-page
	.main-content
	.recommendation-card
	.bottom-section
	.left-section
	.rec-message-span
	a:hover {
	text-decoration: underline;
}

.recommendation-page .interactive-star {
	cursor: pointer;
	background-image: url('data:image/png;base64,');
	background-repeat: no-repeat;
}

.recommendation-page .interactive-star.small {
	background-size: 15px 15px;
	width: 15px;
	height: 15px;
}

.recommendation-page .interactive-star.big {
	background-size: 30px 30px;
	width: 30px;
	height: 30px;
	background-image: url('data:image/svg+xml;base64,');
}

.recommendation-page .interactive-star.big.on {
	background-image: url('data:image/svg+xml;base64,');
}

.recommendation-page .interactive-star.on {
	background-image: url('data:image/png;base64,');
}

.recommendation-page .more-actions-box {
	background-color: rgb(233, 228, 208);
	border-radius: 8px;
	width: 48%;
	padding: 10px;
}

.recommendation-page .more-actions-box .more-actions-title {
	color: rgb(99, 92, 82);
	font-size: 14px;
	font-family: var(--lato-font);
}

.recommendation-page .more-actions-box .anchors-list {
	position: relative;
	background-color: rgb(255, 255, 255);
	margin-top: 15px;
	border-radius: 5px;
}

.recommendation-page .more-actions-box .anchors-list a {
	border-bottom: 1px solid rgb(248, 247, 242);
	height: 17px;
	color: var(--goodreads-teal);
	font-family: var(--lato-font);
	font-size: 12px;
	padding: 5px 10px;
	line-height: 17px;
}

.recommendation-page .more-actions-box .anchors-list a:hover {
	background-color: rgb(248, 247, 242);
}

.recommendation-page .more-actions-box .anchors-list a.selected {
	position: relative;
	background: url('https://www.goodreads.com/assets/layout/navigation_arrow.png');
	background-position: left;
	background-repeat: no-repeat;
	background-size: 99%;
	font-weight: bold;
	color: rgb(255, 255, 255);
	width: 95%;
	left: -31px;
	padding-left: 40px;
	border-bottom: none;
}

.recommendation-page .no-pending-recommendations-span {
	width: 100%;
	font-size: 14px;
	font-family: var(--lato-font);
	color: var(--goodreads-black);
}
